.footer{
    background-image: url(/static/media/footer.7e5aec06.png);
    background-size: cover;
    background-color: rgb(252, 252, 252);
    padding-left: 80px;
    padding-top: 40px;
}
.footer > div:first-child{
    display: flex;
}
.footer > div > *{
    width: 33.33%;
}

.footer .brandIcon > *{
    border: 1px solid #eb1e75;
    border-radius: 15px;
    padding: 5px;
    height: 30px;
    width: 30px;
    margin-right: 10px;
    color: #eb1e75;
    transition: .5s;
}
.footer .brandIcon > *:hover{
    background-color: #eb1e75;
    color: white;
}
.footer .callNow{
    background-image: linear-gradient(to right, #eb1e75, #000000);
    width: 140px;
    padding: 10px 10px;
    color: white;
    border-radius: 5px;
}
.footer small{
    color: gray;
    font-weight: 600;
}
